@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: transparent;
  color: rgb(57, 57, 57);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .nav-list {
  margin-left: auto;
  display: flex;
  color:#896fac;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
  font-size: 30px;
}

h3.logo {
  font-size: 60px;
}

.one-dream {
  width: 100vw;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1.dream {
  font-size: 50px;
}

.somes {
  display: flex;
  width: 90vw;
  justify-content: center;
}

.some-item {
  width: 30vw;
  display: flex;
}

.dream-title {
  font-size: 50px;
}

.dream-type {
  color: #743ece;
}

.dream-feeling {
  /* width: 50%; */

  font-size: 30px;
}


.dream-name {
  font-size: 30px;
}

.dream-tools {
  display: flex;
}

.tool {
  margin-left: 10px;
}

.who {
  margin: 0 20px 0;
}

.whos {
  margin-top: 10px;
}

.form-dream {
  width: 60vw;
  display: flex;
  flex-direction: column;
  font-family: "Roboto-light", sans-serif;
}

.btn-dream {
  width: 10vw;
}

#feel {
  width: 16vw;
  font-size: 20px;
  font-weight: bold;
  color: white;
  margin-top: 14px;
}

#concepts {
  width: 6vw;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.about-some {
  font-size: 20px;
  flex-direction: column;
  margin-top: 10px;
}

#value {
  padding-left: 10px;
  margin-right: 10px;
  border: none;
  background-color: #15041e;
  border-bottom: 1px solid #896fac;
  color:white;
}

#feeling {
  padding-left: 10px;
  border: none;
  font-weight: bold;
  background-color: #15041e;
  border-bottom: 1px solid #896fac;
  color:white;
}

.add-some {
  margin-right: 20px;
}
#some {
  font-weight: bold;
  color: white;
}

#name {
  padding-left: 10px;
  background-color: #15041e;
  border: 0;
  border-bottom: 1px solid #896fac;
  color: white;
}

#name::-webkit-input-placeholder {
  color: white;
}

#name:-ms-input-placeholder {
  color: white;
}

#name::-ms-input-placeholder {
  color: white;
}

#name::placeholder {
  color: white;
}

#email {
  padding-left: 10px;
  background-color: #15041e;
  border: 0;
  border-bottom: 1px solid #896fac;
  margin-top: 20px;
  color: white;
}

#email::-webkit-input-placeholder {
  color: white;
}

#email:-ms-input-placeholder {
  color: white;
}

#email::-ms-input-placeholder {
  color: white;
}

#email::placeholder {
  color: white;
}

#password {
  padding-left: 10px;
  background-color: #15041e;
  border: 0;
  border-bottom: 1px solid #896fac;
  margin-top: 20px;
  color: white;
  margin-bottom:20px;
}

#password::-webkit-input-placeholder {
  color: white;
}

#password:-ms-input-placeholder {
  color: white;
}

#password::-ms-input-placeholder {
  color: white;
}

#password::placeholder {
  color: white;
}
.dream-data {
  display: flex;
  flex-direction: column;
  margin-left: 40px;
}

.dream-thought {
  font-size: 14px;
  color: #896fac;
}

.rv-xy-plot__series--label-text {
  fill: white !important;
}
.rv-xy-plot__axis__tick__text {
  fill: white !important;
}

q,
blockquote {
  quotes: "“" "”" "‘" "’";
  font-style: italic;
}

q:before,
blockquote:before {
  content: open-quote;
}

.btn-dream {
  background-color: #6c41be;
  border: none;
  color: white;
  border-radius: 30px;
  padding: 6px 8px;
  align-self: flex-end;
}

.form-login {
  display: flex;
  flex-direction: column;
}

q:after,
blockquote:after {
  content: close-quote;
}

q:before,
q:after,
blockquote:before,
blockquote:after {
  color: #eb10db;
  font-size: 1.2em;
}

.dreams-fulllist {
  display: flex;
  flex-direction: column;
}

h3.dream-name {
  font-size: 18px;
}

@media (max-width: 400px) {
  .some-item {
    width: 90vw;
    /* height: 36vh; */
    display: flex;
    margin-bottom: 20px;
    margin-top: 20px;
    /* align-items: center; */
    /* justify-content: center; */
  }

  .one-dream {
    width: 100vw;
    padding: 10px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .dream-title {
    font-size: 28px;
  }

  .dream-feeling {
    font-size: 18px;
  }

  .dream-img {
    width: 28vw;
  }
  .somes {
    display: flex;
    width: 90vw;
    padding: 0 16px;
    justify-content: center;
    flex-direction: column;
  }
  h3.logo {
    font-size: 30px;
  }
  .form-dream {
    width: 80vw;
    display: flex;
    flex-direction: column;
  }

  h2.question {
    margin-top: 20px;
    font-size: 17px;
  }

  .btn-dream {
    width: 30vw;
    margin-top: 10px;
  }

  #feel {
    width: 60vw;
  }

  #concepts {
    width: 26vw;
    font-size: 20px;
  }
  h1.dream {
    font-size: 30px;
    text-align: center;
  }
  .who {
    margin: 0 0 0;
  }

  #some {
    font-weight: bold;
    width: 40vw;
  }

  #value {
    padding-left: 10px;
    width: 60vw;
    margin-right: 10px;
    border: none;
    background-color: #15041e;
    border-bottom: 1px solid #896fac;
    color:white;
  }

  #feeling {
    width: 80vw;
  }

  .form-login {
    width: 80vw;
  }

  #email,
  #password,
  #name {
    width: 100%;
  }
  .dreams-list {
    font-size: 26px;
    text-align: center;
  }
}

